const editFieldsData = a => {
	return (
		[
			{
				label: " ",
				type: "text",
				value: a.name,
				name: "name",
			},
			{
				label: "  ",
				type: "text",
				value: a.smallDescription,
				name: "smallDescription",
			},
			{
				label: "  ",
				type: "textarea",
				value: a.bigDescription,
				name: "bigDescription",
			},
			{
				label: "",
				type: "select",
				value: a.category,
				name: "category",
			},
			{
				label: "   ",
				type: "datetime-local",
				value: a.dateTime,
				name: "dateTime",
			}
		]
	);
};

export default editFieldsData;