const fieldsData = n => [
	{
		label: '',
		type: 'text',
		value: n.name,
		name: 'name',
	},
	{
		label: '',
		type: 'text',
		value: n.description,
		name: 'description',
	},
	{
		label: ' ',
		type: 'date',
		value: n.date,
		name: 'date',
	},
];

export default fieldsData;
