import { Link, useNavigate } from 'react-router-dom';
import { Form } from '../../../../../index';

import s from './bookCreate.module.css';
import mainFieldsData from '../MainFieldsData';
import additionalFieldsData from '../additionalFieldsData';

const BookCreate = props => {
	props.setHeader(false);
	const navigate = useNavigate();

	return (
		<div className={s.container}>
			<Link to='/admin/books' className={s.btn}>
				  
			</Link>
			<div className={s.contentBlock}>
				<h2 className={s.caption}>  </h2>
				<div className={s.topBar}>
					<button className={s.addBook}>  URL</button>
					<button className={s.addBook}>  Google</button>
					<button className={s.addBook}>  </button>
					<input
						className={s.inputCodeField}
						type='text'
						placeholder='   '
					/>
				</div>
				<Form
					main={mainFieldsData({})}
					additional={additionalFieldsData({})}
					btns={[
						{
							title: ' ',
							type: 'submit',
							post: 'add-book',
							onclick: () =>
								setTimeout(() => {
									navigate('/admin/books');
									window.scrollTo(0, 0);
								}, 100),
						},
					]}
				/>
			</div>
		</div>
	);
};

export default BookCreate;
