import s from '../../../Popup/popUps.css';
import sp from '../../../Popup/popUps.module.css';
import { qrCode, QrIcon } from '../../img';

const tableData = () => {
	const keys = [
		{
			name: '',
			width: 30,
			property: 'full_name',
			isCounter: true,
			isLink: true,
			path: '/reader/',
		},
		{
			name: '',
			width: 15,
			property: 'phone_number',
		},
		{
			name: 'Email',
			width: 20,
			property: 'email',
		},
		{
			name: ' ',
			width: 15,
			property: 'is_disabled_person',
			align: 'center',
		},
		{
			name: 'QR-',
			width: 10,
			property: 'qr',
			propertyTitle: 'name',
			isPopup: true,
			align: 'center',
			showPopUp() {
				return (
					<>
						<img className={s.qrImg} src={QrIcon} alt='' />
						<button className={sp.btn} onClick={() => {}}>
							
						</button>
					</>
				);
			},
		},
		{
			name: '',
			width: 10,
			property: 'status',
			propertyTitle: 'status',
			align: 'center',
			colors: ['rgb(232, 238, 246)', 'rgb(248, 126, 115)'],
		},
	];

	return keys;
};

export default tableData;
