import s from './addToPrefer.module.css';

const AddToPrefer = props => {
  return (
    <div className={s.container}>
      <button className={s.btn} style={{backgroundColor: '#36BBCB'}}></button>
      <span></span>
      {
        true
          ? <button className={s.btn} style={{backgroundColor: '#bfbfbf'}}> </button>
          : <button className={s.btn} style={{backgroundColor: '#f5a623'}}>  </button>
      }
    </div>
  )
}

export default AddToPrefer;