import { React } from 'react';
import { Link } from 'react-router-dom';
import s from './sideSignIn.module.css';

const SideSignIn = props => {
  return (
    <div className={s.sideSignIn}>
         <Link to="/login" className={s.link}></Link>   
    </div>
  )
}

export default SideSignIn;