import s from '../../../Popup/popUps.css'
import sp from '../../../Popup/popUps.module.css'

import editFieldsData from './editFieldsData.js'
import { Form } from '../../../index'

const tableData = () => {
    const keys = [
        {
            name: " ",
            width: 35,
            property: "name",
            isCounter: true,
        },
        {
            name: "",
            width: 20,
            property: "category"
        },
        {
            name: "   ",
            width: 17,
            property: "dateTime"
        },
        {
            name: "",
            width: 28,
            property: "btns",
            btns: [
                {
                    name: "",
                    color: "#36bbcb",
                    isPopup: true,
                    popupTitle: " ",
                    showPopUp(url, el) {
                        return(
                            <Form main={editFieldsData(el)} btns={ [{ title: "", type: "submit" }] } />
                        )
                    }
                },
                {
                    name: "",
                    color: "rgb(248, 126, 115)",
                    isLink: true,
                },
                {
                    name: " ",
                    color: "rgb(105, 195, 152)",
                    isLink: true,
                    path: '/admin/event-reports/create/'
                }
            ],
            align: "flex-end",
        },
    ]

    return (
        keys
    )
}

export default tableData;