import s from '../../../Popup/popUps.css';
import sp from '../../../Popup/popUps.module.css';
import { qrCode, QrIcon } from '../../img';

const tableData = () => {
	const keys = [
		{
			name: ' ',
			width: 30,
			property: 'title',
			isCounter: true,
			isLink: true,
			path: '/book-single/',
		},
		{
			name: '',
			width: 25,
			property: 'author',
		},
		{
			name: ' ',
			width: 15,
			property: 'isbn',
		},
		{
			name: '',
			width: 10,
			property: 'status',
			propertyTitle: 'bookStatus',
			align: 'center',
			colors: [
				'rgb(232, 238, 246)', //   
				'rgb(248, 126, 115)', //  
			],
		},
		{
			name: 'QR-',
			width: 12,
			property: 'qr',
			propertyTitle: 'bookName',
			isPopup: true,
			align: 'center',
			showPopUp(url) {
				return (
					<>
						<img src={QrIcon} alt='' />
						<button className={sp.btn} onClick={() => {}}>
							
						</button>
					</>
				);
			},
		},
		{
			name: '',
			width: 8,
			property: 'btns',
			btns: [
				{
					path: '/book-single/edit/',
					name: '',
					color: '#36bbcb',
					isLink: true,
				},
			],
			align: 'flex-end',
		},
	];

	return keys;
};

export default tableData;
