import { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import Form from '../../../../Form/Form';
import s from './CreateEventReport.module.css';
import fieldsData from './fieldsData';

const CreateEventReport = props => {
	useEffect(() => {
		document.title = '';
	}, []);
	props.setHeader(false);
	const { id } = useParams();
	const [eventReport, setEventReport] = useState({
		id: id,
		target: '',
		place: '',
		attends: '',
		techUse: '',
		efficiency: '',
		formOfWork: '',
		responsibles: '',
		conclusions: '',
		usedBooks: '',
		participants: '',
	});
	return (
		<div className={s.container}>
			<div className={s.header}>
				<Link to='/admin/activities'></Link>
				<h1>   </h1>
			</div>
			<Form main={fieldsData(eventReport)} btns={[{ title: ' ' }]} />
		</div>
	);
};

export default CreateEventReport;
